$color_1: #000;
$color_2: var(--accent-text-color);
$color_3: var(--main-text-color);
$color_4: var(--icon-color);
$color_5: var(--main-white-color);
$color_6: var(--accent-grey);
$font-family_1: "TT Lakers", serif;
$background-color_1: var(--background-color);
$background-color_2: var(--main-white-color);

@font-face {
  font-family: "TT Lakers";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/TTLakes-Regular.woff") format("woff");
}

@font-face {
  font-family: "TT Lakers";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/TTLakes-Medium.woff") format("woff");
}

@font-face {
  font-family: "TT Lakers";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/TTLakes-Bold.woff") format("woff");
}

:root {
  --main-white-color: #ffffff;
  --main-text-color: #1B1C21;
  --accent-color: #41F6D7;
  --accent-text-color: #7E7E82;
  --accent-block-line: #D9FFF5;
  --accent-grey: #B5B6BC;
  --icon-color: #FF3E79;
  --background-color: #F8F8F8;
  --border-color: #EAEAEA;
}

.body {
  background-color: $background-color_1;
  margin: 0;
  padding: 0;
  position: relative;
}

.body--no-scroll {
  overflow: hidden;
  height: 100vh;
}

.main-font {
  font-family: $font-family_1;
}

.button-reset {
  border: none;
  cursor: pointer;
  background: var(--main-white-color);
}

.text-for-768px {
  display: none;
}

.text-for-1024px {
  display: none;
}

.main__nav-1024px {
  display: none;
}

.list-and-link-reset {
  list-style-type: none;
  margin: 0;
  padding: 0;
  color: $color_1;
  text-decoration: none;
}

.container {
  max-width: 1120px;
  margin: 0 auto;
}

.header-mobile {
  margin-top: 8px;
  margin-left: 1px;
  background: var(--main-white-color);
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 16px;

  .button-menu {
    &:last-of-type {
      margin-left: auto;
    }
  }
}

.logo {
  display: flex;
  justify-content: center;
  transition: opacity 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7;
  }
}

.button-menu {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--icon-color);
  cursor: pointer;
  transition: opacity 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7;
  }
}

.header__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 24px 16px;
  background: var(--main-white-color);
  border-bottom: 1px solid var(--accent-block-line);
}

.header__left {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
}

.nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 16px;
}

.header__nav {
  &::before {
    content: "";
    width: 2px;
    height: 32px;
    background: var(--border-color);
    align-self: center;
    border-radius: 2px;
  }

  button {
    &:nth-child(-n+3) {
      display: none;
    }
  }
}

.main__title {
  position: relative;
  padding-left: 14px;
  font-size: 28px;

  &::before {
    content: "";
    position: absolute;
    left: -2px;
    top: 50%;
    transform: translateY(-50%);
    width: 5px;
    height: 40px;
    border-radius: 30%;
    background: var(--accent-color);
  }
}

.main__navigation {
  overflow: auto;
}

.main__navigation-list {
  display: flex;
  flex-direction: row;
  padding: 0;
  margin-left: 16px;
}

.main__navigation-list-item {
  padding: 8px 16px;
  background: var(--background-color);
}

.main__navigation-list-item-link {
  font-weight: 500;
  white-space: nowrap;
  color: $color_2;
  background: var(--background-color);
  line-height: 24px;
  letter-spacing: -0.2px;
  font-size: 16px;
}

.main__navigation-list-item--active {
  border: 2px solid var(--accent-color);
  background: var(--main-white-color);
  border-radius: 6px;
}

.main__navigation-list-item-link--active {
  background: var(--main-white-color);
}

.main__content {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main__content-text {
  margin: 0 16px;
  padding-bottom: 16px;
  font-size: 14px;
}

.main__content-read-more {
  margin-left: 16px;
  margin-bottom: 16px;
  align-self: flex-start;
  position: relative;
  padding-left: 30px;
  font-size: 16px;
  background: var(--background-color);
  transition: opacity 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7; // Теперь есть изменение
  }

  &::before {
    content: url("../img/icon_scroll-arrows.svg");
    position: absolute;
    left: 5px;
    top: 50%;
    transform: translateY(-50%);
  }
}

.main__content-img {
  width: 100%;
  height: auto;
  display: block;
}

.aside {
  height: 100%;
  background-color: $background-color_2;
  box-sizing: border-box;
  width: 100%;
  max-width: 360px;
  box-shadow: 16px 0 52px 0 #0E185033, -2px 0 4px #454F7E05;
  padding: 16px 15px 32px 15px;
  position: absolute;
  z-index: 1000;
  visibility: hidden;
}

.aside--active {
  visibility: visible !important;
  z-index: 5000;
}

.aside__top {
  display: flex;
  flex-direction: column;
}

.page-nav__list {
  display: flex;
  flex-direction: column;
  gap: 32px;
  margin: 46px 0 32px 0;
}

.page-nav__link {
  font-size: 16px;
  font-family: $font-family_1;
  cursor: pointer;
  font-weight: 500;
  line-height: 24px;
  color: $color_3;
  transition: opacity 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7;
  }
}

.page-nav__link--active {
  color: $color_2;
  position: relative;
}

.page-nav__item--active {
  &::before {
    content: "";
    position: absolute;
    left: -33px;
    top: 50%;
    transform: translateY(-50%);
    width: 5px;
    height: 40px;
    border-radius: 30%;
    background: var(--accent-color);
  }
}

.aside-footer {
  display: flex;
  flex-direction: column !important;
  gap: 15px;
  align-items: start !important;
}

.aside-footer__list {
  display: flex;
  flex-direction: row;
  gap: 16px;
}

.footer__contacts {
  font-style: normal;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.footer__contacts-mail {
  font-family: $font-family_1;
  font-size: 16px;
  line-height: 24px;
  transition: opacity 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7;
  }
}

.footer__contacts-phone {
  font-family: $font-family_1;
  font-size: 20px;
  font-weight: bold;
  transition: opacity 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7;
  }
}

.languages-list {
  display: flex;
  flex-direction: row;
  gap: 5px;
  padding-bottom: 15px;
}

.languages-list__item {
  transition: background-color 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7;
  }
}

.languages-link {
  font-size: 16px;
  font-weight: 500;
  font-family: $font-family_1;
}

.languages-link--active {
  color: $color_2;
  font-weight: 400;
}

.repair-block {
  background: var(--background-color);
}

.repair-block__title {
  margin-top: 24px;
  padding: 16px;
  font-family: $font-family_1;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
  color: $color_2;
  background: linear-gradient(to right, #F8F8F8 0%, #ffffff 100%);
  border-top: 1px solid var(--accent-block-line);
}

.slider {
  position: relative;
  height: auto;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 100%;
    transform: translate(0, -35%);
    z-index: 100;
    background: linear-gradient(to right, rgba(248, 248, 248, 0), rgba(248, 248, 248, 100));
    pointer-events: none;
  }
}

.slider__wrapper {
  padding-left: 16px;
  padding-bottom: 40px;
}

.slider__slide {
  background: var(--main-white-color);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  display: flex !important;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  transition: opacity 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7;
    filter: brightness(1.2);
  }
}

.slider__slide-logo {
  padding: 20px 0;
  width: 100px;
  height: 35px;
}

.slider__slide-button {
  display: block;
  color: $color_4;
  font-size: 20px;
  font-weight: 600;
  border: 2px solid var(--icon-color);
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.slider__pagination {
  position: relative;
}

.swiper-pagination-bullet {
  width: 12px !important;
  height: 12px !important;
}

.swiper-pagination-bullet-active {
  background: var(--accent-grey) !important;
}

.show-all-button {
  display: none;
}

.slide-tech {
  height: 160px !important;
  align-items: normal;
}

.slide-tech__title {
  font-weight: 500;
}

.slide-tech__button {
  align-self: end;
  margin-bottom: 16px;
}

.slider-price {
  display: flex;
  flex-direction: column;
  align-items: normal;
  padding: 16px;
  box-sizing: border-box;
  gap: 16px;
  height: 250px;
  position: relative;
}

.slider-info__title {
  color: $color_2;
  font-weight: 400;
  margin: 0 0 4px 0;
}

.slider-info__text {
  margin: 0;
}

.slider__info-buy-btn {
  width: 120px;
  height: 32px;
  text-transform: uppercase;
  color: $color_5;
  font-weight: 600;
  font-family: $font-family_1;
  border-radius: 16px;
  background: var(--icon-color);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  transition: opacity 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7;
    filter: brightness(1.2);
  }
}

.btn-slider-position {
  position: absolute;
  bottom: 15px;
  right: 15px;
}

.about-price {
  padding: 0 16px;
  margin-top: 16px;
}

.call-to-action {
  margin-top: 16px;
  padding: 0 16px;
  display: block;
  font-weight: 500;
  transition: opacity 0.3s ease, filter 0.3s ease;

  &:hover {
    opacity: 0.7;
    filter: brightness(1.2);
  }
}

.dropdown {
  position: relative;
  top: 2px;
  width: 10px;
  left: 10px;
}

.footer {
  padding: 10px;
  margin-top: 40px;
  display: flex;
  gap: 16px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: 1px solid var(--accent-block-line);
}

.footer-info {
  text-align: center;
  color: $color_2;
  font-size: 14px;
}

.block-prices {
  display: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--main-white-color);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
  z-index: 999;
}

.overlay--active {
  opacity: 0.95;
  visibility: visible;
}

.aside-right {
  max-width: 440px;
  position: absolute;
  right: 0;
  z-index: 2000;
}

.aside-feedback {
  visibility: hidden;
  height: 100vh;
  background-color: $background-color_2;
  padding: 24px 16px;
}

.aside-order {
  visibility: hidden;
  height: 100vh;
  background-color: $background-color_2;
  padding: 24px 16px;
}

.aside-right__title {
  margin-top: 32px;
  font-size: 24px;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    left: -18px;
    top: 50%;
    transform: translateY(-50%);
    width: 5px;
    height: 40px;
    border-radius: 30%;
    background: var(--accent-color);
  }
}

.aside-right__form {
  margin-top: 24px;

  > label {
    visibility: hidden;
  }
}

.form-input {
  margin-bottom: 16px;
  width: 90%;
  height: 40px;
  border-radius: 8px;
  border: 1px solid var(--border-color);
  color: $color_6;
  font-family: $font-family_1;
  font-size: 16px;
  padding-left: 16px;

  &::-webkit-input-placeholder {
    position: absolute;
    top: 20%;
    left: 10px;
    font-size: 16px;
    color: $color_6;
    font-family: $font-family_1;
    font-weight: 400;
  }
}

.form-textarea {
  &::placeholder {
    top: 10%;
  }

  height: 120px;
  padding-top: 10px;
}

.aside-right__info {
  margin-top: 0;
  font-size: 14px;
  width: 95%;
  font-weight: 400;
}

.aside-right__info-link {
  color: $color_4;
  cursor: pointer;
}

.feedback__btn {
  font-size: 12px;
  position: absolute;
  right: 8%;
}

@media (min-width: 768px) {
  .aside {
    max-width: 320px;
  }
  .aside-footer {
    margin-top: 150px;
  }
  .aside-feedback__close-btn {
    position: absolute;
    left: -60px;
    top: 30px;
  }
  .aside-order__close-btn {
    position: absolute;
    left: -60px;
    top: 30px;
  }
  .aside-right__title {
    margin-top: 10px;
  }
  .header__nav {
    button {
      &:nth-child(-n+3) {
        display: flex;
        align-items: center;
        justify-content: center;
      }

      &:nth-child(4) {
        margin-left: 15px;

        &::before {
          content: "";
          width: 2px;
          height: 32px;
          background: #EAEAEA;
          align-self: center;
          border-radius: 2px;
          left: -25px;
          position: relative;
        }
      }
    }

    &::before {
      display: none;
    }
  }
  .main__content {
    flex-direction: row;
    margin-top: 32px;
    justify-content: space-between;
  }
  .main__content-left-side {
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .main__content-img {
    border-radius: 10px;
  }
  .text-for-768px {
    display: block;
  }
  .repair-block__title {
    padding-right: 0;
    padding-left: 16px;
  }
  .slide-logo {
    &:nth-child(n+7) {
      display: none !important;
    }
  }
  .slider__wrapper {
    display: grid !important;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 16px;
    column-gap: 24px;
    padding-right: 10px;
    width: 95% !important;
  }
  .slider {
    &::after {
      display: none;
    }
  }
  .show-all-button {
    display: block;
  }
  .rotated {
    &::before {
      rotate: 180deg;
      top: 0;
      transform: translateY(0);
    }
  }
  .slide-tech {
    &:nth-child(n+4) {
      display: none !important;
    }
  }
  .swiper-3 {
    display: none !important;
  }
  .block-prices {
    display: grid;
    grid-template-columns: 400px 1fr 1fr 1fr;
    grid-template-rows: 50px;
    grid-auto-rows: 80px;
    margin-left: 24px;
    margin-right: 24px;

    .block-prices-item {
      &:nth-child(5) {
        border-radius: 15px 0 0 0;
      }
    }
  }
  .block-prices__title {
    color: $color_2;
    font-size: 16px;
    background: var(--background-color);
    padding-left: 16px;
  }
  .block-prices-item {
    background-color: $background-color_2;
    padding-left: 16px;
    padding-top: 25px;
    border-bottom: 1px solid var(--border-color);

    &:nth-child(8) {
      border-radius: 0 15px 0 0;
    }

    &:nth-last-child(4) {
      border-radius: 0 0 0 15px;
    }

    &:last-child {
      border-radius: 0 0 15px 0;
    }
  }
  .main-footer {
    margin-top: 24px;
    max-width: 500px;
  }
  .footer {
    flex-direction: row;
    gap: 30px;
    align-items: start;
    justify-content: space-between;
  }
  .footer-info {
    text-align: start;
    flex-basis: 250px;

    &:last-of-type {
      text-align: end;
    }
  }
}

@media (min-width: 1024px) {
  .main__header-container {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .main__nav-1024px {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 160px;
  }
  .main__nav-link {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background: var(--icon-color);
    border-radius: 50%;
    font-size: 16px;
    transition: opacity 0.3s ease, filter 0.3s ease;

    &:hover {
      opacity: 0.7;
      filter: brightness(1.2);
    }
  }
  .main__nav-text {
    position: absolute;
    left: -135px;
    color: $color_3;
    font-weight: 500;
    transition: opacity 0.3s ease, filter 0.3s ease;

    &:hover {
      opacity: 0.7;
      filter: brightness(1.2);
    }
  }
  .text-for-768px {
    display: none;
  }
  .text-for-1024px {
    display: block;
  }
  .main__navigation-list {
    > li {
      &:nth-child(-n+5) {
        display: block;
      }

      &:nth-child(3) {
        display: none;
      }
    }

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .main__navigation-list-item {
    cursor: pointer;

    &:hover {
      border: 2px solid var(--accent-color);
      background: var(--main-white-color);
      border-radius: 6px;

      .main__navigation-list-item-link {
        background: var(--main-white-color);
      }
    }
  }
  .main__navigation-list-item--active {
    &:nth-child(2) {
      border: none;
    }
  }
  .main__navigation {
    overflow: unset;
  }
  .slider__wrapper {
    max-width: 1120px;
    grid-template-columns: repeat(4, 1fr);
  }
  .slide-logo {
    &:nth-child(n-9) {
      display: flex !important;
    }

    &:nth-child(n+9) {
      display: none !important;
    }
  }
  .open-aside-btn {
    transition: opacity 0.3s ease, filter 0.3s ease;

    &:hover {
      opacity: 0.7;
      filter: brightness(1.2);
    }
  }

  .slide-tech {
    &:nth-child(n-5) {
      display: flex !important;
    }

    &:nth-child(n+5) {
      display: none !important;
    }
  }
}

@media (min-width: 1440px) {
  .container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    max-width: 1440px;
  }
  .aside {
    height: auto;
    visibility: visible;
    position: static;
    box-shadow: none;
  }
  .header__container {
    display: none;
  }
}
